



















































.code {
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
  margin: 0 0 1rem ;
  padding: 0;
  display: block;
  font-family: monospace;
  cursor: pointer;
}

.code .heading {
  display: block;
  font-size: 11px;
  letter-spacing: 1px;
  margin-bottom: 5px;
  text-transform: uppercase;
  color: #ff3860;
}

.code .title {
  color: #363636;
  font-size: 28px;
  line-height: 1;
  font-weight: 300;
  word-wrap: break-word;
}

@media screen and (max-width: 320px) {
  .code {
    width: 50%;
  }
}

@media screen and (min-width: 321px) and (max-width: 512px) {
  .code {
    width: 25%;
  }
}

@media screen and (min-width: 513px)  and (max-width: 1024px) {
  .code {
    width: 16.66667%;
  }
}

@media screen and (min-width: 1025px) {
  .code {
    width: 12.5%;
  }
}



