





























































































































































































article {
  word-wrap: break-word;
}

article .message-header {
  cursor: pointer;
}

.notification .copy {
  border-radius: 0 3px;
  float: right;
  margin: -16px -20px 0 20px;
}

.notification.is-code, code.morse-code {
  background: transparent;
}

.notification.is-code {
  color: black;
}

#input-box {
  font-family: monospace;
  font-size: 1rem;
  line-height: 1.5rem;
}



